import helper from '../../common/common.js';
export default {
	data() {
		return {
			content:'',
			contact_info:'',
			issubmit:true,
			windowHeight: ''
		}
	},
	onLoad() {
	    uni.getSystemInfo({
	        success: (res)=> {
	            this.windowHeight = res.windowHeight;
	        }
	    });    
	    uni.onWindowResize((res) => {
	        if(res.size.windowHeight+200 < this.windowHeight){
	            this.issubmit = false
	        }else{
	            this.issubmit = true
	        }
	    })
	},
	methods: {
		back(){
			uni.switchTab({
			    url: '/pages/my/my'
			});
		},
		content1(e){
			this.content = e.detail.value;
		},
		content2(e){
			this.content_info = e.detail.value;
		},
		callback(Error){
			 console.log("name",Error);
			this.$refs.uToast.show({
				title: Error,
				type: 'default',
				// url: '../tellogin/tellogin'
			})
			 
		},
		submit(){
			if(this.content == ''){
				this.callback('内容不能为空');
				return
			}else{
				uni.request({
					url:helper.websiteUrl+"service_feelback",
					header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
					method:"post",
					data:{
						'content':this.content,
						'contact_info':this.contact_info
					},
					success: (res) => {
						console.log(res)
						if(res.data.code==200 && this.content != ''){
							this.callback('提交成功');
							setTimeout(() => {
								uni.switchTab({
									url: '/pages/my/my'
								});
							}, 1000);
						}else{
							this.callback('提交失败');
						}
						return
						
					}
				});
			}
			
			
		}
	}
}
